import React, { useState, useMemo, useEffect } from 'react';
import { Search, Filter, Grid, List, Calendar, User, Building, Tag, Eye, X, EyeOff, Clock, ChevronDown, ChevronUp, Gamepad2, Puzzle, Dice5, Image as ImageIcon, Package, Users, ExternalLink } from 'lucide-react';
import { GameData } from '../types/GameData';
import { gameApiService } from '../services/api';
import { hiddenTags, defaultExcludedTags, defaultIncludedTags, mainViewHiddenTags } from '../tagsConfig.ts';

const BoardGameCatalog: React.FC = () => {
    // State to store game data from API
    const [games, setGames] = useState<GameData[]>([]);
    const [ , setLoading] = useState<boolean>(true);
    const [ , setError] = useState<string | null>(null);

    // Fetch game data from API service on component mount
    useEffect(() => {
        const fetchGames = async () => {
            try {
                setLoading(true);
                const gamesData = await gameApiService.getAllGames();
                setGames(gamesData);
                setError(null);
            } catch (err) {
                setError('Nie udao si zaadowa gier. Sprbuj ponownie pniej.');
                console.error('Error fetching games:', err);
            } finally {
                setLoading(false);
            }
        };

        fetchGames();
    }, []);

    const [searchTerm, setSearchTerm] = useState<string>('');
    const [selectedTags, setSelectedTags] = useState<string[]>(defaultIncludedTags);
    const [excludedTags, setExcludedTags] = useState<string[]>(defaultExcludedTags);
    const [sortBy, setSortBy] = useState<string>('title');
    const [viewMode, setViewMode] = useState<'grid' | 'list'>('grid');
    const [showFilters, setShowFilters] = useState<boolean>(false);
    const [showExcludeFilters, setShowExcludeFilters] = useState<boolean>(false);
    
    // Component filter states
    const [filterBoardGames, setFilterBoardGames] = useState<boolean>(false);
    const [filterCardGames, setFilterCardGames] = useState<boolean>(false);
    const [filterDiceGames, setFilterDiceGames] = useState<boolean>(false);
    const [filterFigurineGames, setFilterFigurineGames] = useState<boolean>(false);
    const [filterPieceGames, setFilterPieceGames] = useState<boolean>(false);

    // Extract all unique values for facets
    const allTags = useMemo(() => {
        const tags = new Set<string>();
        games.forEach(game => {
            if (game.tags) {
                game.tags.forEach(tag => tags.add(tag));
            }
        });
        // Get the array of unique tags
        const tagsArray = Array.from(tags);

        // Calculate the count for each tag
        const counts: Record<string, number> = {};
        games.forEach(game => {
            if (game.tags) {
                game.tags.forEach(tag => {
                    counts[tag] = (counts[tag] || 0) + 1;
                });
            }
        });

        // Sort tags by occurrence count (descending)
        tagsArray.sort((a, b) => {
            return (counts[b] || 0) - (counts[a] || 0);
        });

        return tagsArray.filter(tag => !hiddenTags.includes(tag)); // Filter out hidden tags
    }, [games]);

    // Calculate tag counts
    const tagCounts = useMemo(() => {
        const counts: Record<string, number> = {};
        games.forEach(game => {
            if (game.tags) {
                game.tags.forEach(tag => {
                    counts[tag] = (counts[tag] || 0) + 1;
                });
            }
        });
        return counts;
    }, [games]);

    // Filter and sort games
    const filteredGames = useMemo(() => {
        let filtered = games.filter(game => {
            // Search only in title
            const matchesSearch = searchTerm === '' ||
                game.title.toLowerCase().includes(searchTerm.toLowerCase());

            // Tag filter
            const matchesTags = selectedTags.length === 0 ||
                selectedTags.every(tag => game.tags?.includes(tag));

            // Exclusion filter - hide games that have ANY excluded tag
            const matchesExclusion = excludedTags.length === 0 ||
                !excludedTags.some(tag => game.tags?.includes(tag));

            // Component filters
            const matchesComponentFilters = 
                (!filterBoardGames || game.boardCount > 0) &&
                (!filterCardGames || game.cardCount > 0) &&
                (!filterDiceGames || game.diceCount > 0) &&
                (!filterFigurineGames || game.figurineCount > 0) &&
                (!filterPieceGames || game.pieceCount > 0);

            return matchesSearch && matchesTags && matchesExclusion && matchesComponentFilters;
        });

        // Sort games
        filtered.sort((a, b) => {
            switch (sortBy) {
                case 'title':
                    return a.title.localeCompare(b.title);
                case 'author':
                    return a.author.localeCompare(b.author);
                case 'year':
                    return parseInt(b.publisherYear) - parseInt(a.publisherYear);
                case 'publisher':
                    return a.publisherName.localeCompare(b.publisherName);
                default:
                    return 0;
            }
        });

        return filtered;
    }, [games, searchTerm, selectedTags, excludedTags, sortBy, filterBoardGames, filterCardGames, filterDiceGames, filterFigurineGames, filterPieceGames]);

    const toggleTag = (tag: string) => {
        setSelectedTags(prev =>
            prev.includes(tag)
                ? prev.filter(t => t !== tag)
                : [...prev, tag]
        );
    };

    const toggleExcludedTag = (tag: string) => {
        // If adding to excluded tags, also remove from selected tags
        if (!excludedTags.includes(tag)) {
            setSelectedTags(prev => prev.filter(t => t !== tag));
        }

        setExcludedTags(prev =>
            prev.includes(tag)
                ? prev.filter(t => t !== tag)
                : [...prev, tag]
        );
    };

    const clearFilters = () => {
        setSearchTerm('');
        setSelectedTags([]);
        setExcludedTags([]);
        setFilterBoardGames(false);
        setFilterCardGames(false);
        setFilterDiceGames(false);
        setFilterFigurineGames(false);
        setFilterPieceGames(false);
    };

    const GameCard: React.FC<{ game: GameData }> = ({ game }) => {
        const [showAllTags, setShowAllTags] = useState(false);
        const [showFullDescription, setShowFullDescription] = useState(false);
        const filteredTags = useMemo(() =>
            game.tags?.filter(tag => !mainViewHiddenTags.includes(tag)) || [],
            [game.tags]
        );

        // Check if game has any component counts
        const hasComponentCounts = game.boardCount || game.figurineCount || game.cardCount || game.diceCount || game.pieceCount;

        return (
        <div className="bg-white rounded-xl shadow-md hover:shadow-xl transition-all duration-300 overflow-hidden border border-gray-200">
            <div className="relative">
                <img
                    src={game.imageUrl?.replace('http://', 'https://') || ''}
                    alt={game.title}
                    className="w-full h-48 object-cover"
                    onError={(e) => {
                        const target = e.target as HTMLImageElement;
                        target.style.display = 'none';
                        const parent = target.parentElement;
                        if (parent && !parent.querySelector('.fallback-icon')) {
                            const fallback = document.createElement('div');
                            fallback.className = 'fallback-icon w-full h-48 bg-gray-100 flex items-center justify-center';
                            fallback.innerHTML = '<svg class="w-16 h-16 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z"></path></svg>';
                            parent.appendChild(fallback);
                        }
                    }}
                />
            </div>

            <div className="p-5">
                <h3 className="text-lg font-bold text-gray-900 mb-2 line-clamp-2">{game.title}</h3>

                <div className={`space-y-2 text-sm text-gray-600 mb-4 ${showFullDescription ? '' : 'hidden md:block'}`}>
                    {game.author && (
                        <div className="flex items-center gap-2">
                            <User className="w-4 h-4 flex-shrink-0" />
                            <span className="truncate">{game.author}</span>
                        </div>
                    )}
                    <div className="flex items-center gap-2">
                        <Building className="w-4 h-4 flex-shrink-0" />
                        <span className="truncate">
                            {game.publisherName}
                            {game.publisherYear && `, ${game.publisherYear}`}
                            {game.publisherCity && `, ${game.publisherCity}`}
                        </span>
                    </div>
                </div>

                <div className="space-y-2 text-sm text-gray-600 mb-4">
                    {/* Age and play time information - show only if either exists */}
                    {(game.minAge || game.playTimeMin || game.minPlayers) && (
                        <div className="flex items-center gap-4">
                            {game.minAge && (
                                <div className="flex items-center gap-2">
                                    <User className="w-4 h-4 flex-shrink-0" />
                                    <span>{game.minAge}{game.maxAge ? `-${game.maxAge}` : '+'}</span>
                                </div>
                            )}
                            {game.playTimeMin && (
                                <div className="flex items-center gap-2">
                                    <Clock className="w-4 h-4 flex-shrink-0" />
                                    <span>
                                        {game.playTimeMax && game.playTimeMin !== game.playTimeMax
                                            ? `${game.playTimeMin}-${game.playTimeMax} min`
                                            : `${game.playTimeMin} min`}
                                    </span>
                                </div>
                            )}
                            {game.minPlayers && (
                                <div className="flex items-center gap-2">
                                    <Users className="w-4 h-4 flex-shrink-0" />
                                    <span>
                                        {game.maxPlayers && game.minPlayers !== game.maxPlayers
                                            ? `${game.minPlayers}-${game.maxPlayers}`
                                            : `${game.minPlayers}`}
                                    </span>
                                </div>
                            )}
                        </div>
                    )}

                    {/* Game Components - show only if any exist */}
                    {hasComponentCounts && (
                        <div className="mt-2 pt-2 border-t border-gray-100">
                            <div className="text-xs font-medium text-gray-500 mb-1">Komponenty gry:</div>
                            <div className="flex flex-wrap gap-3">
                                {game.boardCount && game.boardCount > 0 && (
                                    <div className="flex items-center gap-1" title={`${game.boardCount} ${game.boardCount === 1 ? 'plansza' : game.boardCount < 5 ? 'plansze' : 'plansz'}`}>
                                        <Gamepad2 className="w-3 h-3 flex-shrink-0" />
                                        <span className="text-xs">{game.boardCount}</span>
                                    </div>
                                )}
                                {game.pieceCount && game.pieceCount > 0 && (
                                    <div className="flex items-center gap-1" title={`${game.pieceCount} ${game.pieceCount === 1 ? 'element' : game.pieceCount < 5 ? 'elementy' : 'elementw'}`}>
                                        <Puzzle className="w-3 h-3 flex-shrink-0" />
                                        <span className="text-xs">{game.pieceCount}</span>
                                    </div>
                                )}
                                {game.diceCount && game.diceCount > 0 && (
                                    <div className="flex items-center gap-1" title={`${game.diceCount} ${game.diceCount === 1 ? 'ko' : game.diceCount < 5 ? 'koci' : 'koci'}`}>
                                        <Dice5 className="w-3 h-3 flex-shrink-0" />
                                        <span className="text-xs">{game.diceCount}</span>
                                    </div>
                                )}
                                {game.cardCount && game.cardCount > 0 && (
                                    <div className="flex items-center gap-1" title={`${game.cardCount} ${game.cardCount === 1 ? 'karta' : game.cardCount < 5 ? 'karty' : 'kart'}`}>
                                        <ImageIcon className="w-3 h-3 flex-shrink-0" />
                                        <span className="text-xs">{game.cardCount}</span>
                                    </div>
                                )}
                                {game.figurineCount && game.figurineCount > 0 && (
                                    <div className="flex items-center gap-1" title={`${game.figurineCount} ${game.figurineCount === 1 ? 'figurka' : game.figurineCount < 5 ? 'figurki' : 'figurek'}`}>
                                        <User className="w-3 h-3 flex-shrink-0" />
                                        <span className="text-xs">{game.figurineCount}</span>
                                    </div>
                                )}
                                {/* Total component count is now hidden */}
                            </div>
                        </div>
                    )}
                </div>

                {game.description && (
                    <div className="mb-4">
                        <div className={`text-sm text-gray-700 overflow-hidden transition-all duration-300 ${showFullDescription ? '' : 'line-clamp-2'}`}>
                            {game.description}
                        </div>
                    </div>
                )}
                
                {/* Show more button - visible on mobile to reveal author/publisher info, or when description exists */}
                <div className="mb-4 md:hidden">
                    <button
                        onClick={() => setShowFullDescription(!showFullDescription)}
                        className="text-xs flex items-center text-blue-600 hover:text-blue-800"
                    >
                        {showFullDescription ? (
                            <>Poka mniej <ChevronUp className="ml-1 w-3 h-3" /></>
                        ) : (
                            <>Poka wicej <ChevronDown className="ml-1 w-3 h-3" /></>
                        )}
                    </button>
                </div>

                {/* Show more button on desktop only when description exists */}
                {game.description && (
                    <div className="mb-4 hidden md:block">
                        <button
                            onClick={() => setShowFullDescription(!showFullDescription)}
                            className="text-xs flex items-center text-blue-600 hover:text-blue-800"
                        >
                            {showFullDescription ? (
                                <>Poka mniej <ChevronUp className="ml-1 w-3 h-3" /></>
                            ) : (
                                <>Poka wicej <ChevronDown className="ml-1 w-3 h-3" /></>
                            )}
                        </button>
                    </div>
                )}

                {filteredTags.length > 0 && (
                    <div className="mb-4">
                        <div className="flex flex-wrap gap-1 mb-1">
                            {filteredTags
                                .slice(0, showAllTags ? filteredTags.length : 3)
                                .map(tag => (
                                <span key={tag} className="px-2 py-1 bg-blue-100 text-blue-800 text-xs rounded-full">
                                    {tag}
                                </span>
                            ))}
                        </div>

                        {filteredTags.length > 3 && (
                            <button
                                onClick={() => setShowAllTags(!showAllTags)}
                                className="text-xs text-blue-600 hover:text-blue-800 mt-1 flex items-center"
                            >
                                {showAllTags ? (
                                    <>Poka mniej <X className="w-3 h-3 ml-1" /></>
                                ) : (
                                    <>Poka wszystkie {filteredTags.length} tagi</>
                                )}
                            </button>
                        )}
                    </div>
                )}

                <a
                    href={`http://46.248.162.95/opac/detail.php?PozycjaOpisy=${game.detailId}`}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="w-full bg-blue-600 hover:bg-blue-700 text-white py-2 px-4 rounded-lg font-medium transition-colors duration-200 flex items-center justify-center gap-2"
                >
                    <ExternalLink className="w-4 h-4" />
                    Szczegy
                </a>
            </div>
        </div>
    );
    };

    const GameRow: React.FC<{ game: GameData }> = ({ game }) => {
        const [showAllTags, setShowAllTags] = useState(false);
        const [showFullDescription, setShowFullDescription] = useState(false);
        const filteredTags = useMemo(() =>
            game.tags?.filter(tag => !mainViewHiddenTags.includes(tag)) || [],
            [game.tags]
        );

        // Check if game has any component counts
        const hasComponentCounts = game.boardCount || game.figurineCount || game.cardCount || game.diceCount || game.pieceCount;

        return (
        <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-4 hover:shadow-md transition-shadow duration-200">
            <div className="flex items-center gap-4">
                <img
                    src={game.imageUrl?.replace('http://', 'https://') || ''}
                    alt={game.title}
                    className="w-16 h-16 object-cover rounded-lg flex-shrink-0"
                    onError={(e) => {
                        const target = e.target as HTMLImageElement;
                        target.style.display = 'none';
                        const parent = target.parentElement;
                        if (parent && !parent.querySelector('.fallback-icon-small')) {
                            const fallback = document.createElement('div');
                            fallback.className = 'fallback-icon-small w-16 h-16 bg-gray-100 rounded-lg flex items-center justify-center flex-shrink-0';
                            fallback.innerHTML = '<svg class="w-8 h-8 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z"></path></svg>';
                            parent.appendChild(fallback);
                        }
                    }}
                />

                <div className="flex-1 grid grid-cols-1 md:grid-cols-4 gap-4 items-start">
                    <div>
                        <h3 className="font-semibold text-gray-900">{game.title}</h3>
                        {game.author && <p className="text-sm text-gray-600">{game.author}</p>}

                        {game.description && (
                            <div className="mt-2">
                                <div className={`text-xs text-gray-700 overflow-hidden transition-all duration-300 ${showFullDescription ? '' : 'line-clamp-1'}`}>
                                    {game.description}
                                </div>
                                <button
                                    onClick={() => setShowFullDescription(!showFullDescription)}
                                    className="mt-1 text-xs flex items-center text-blue-600 hover:text-blue-800"
                                >
                                    {showFullDescription ? (
                                        <>Poka mniej <ChevronUp className="ml-1 w-3 h-3" /></>
                                    ) : (
                                        <>Poka wicej <ChevronDown className="ml-1 w-3 h-3" /></>
                                    )}
                                </button>
                            </div>
                        )}
                    </div>

                    <div className="text-sm text-gray-600">
                        <p>{game.publisherName}</p>
                        <p>{game.publisherYear}</p>
                        {/* Age and play time information in one row */}
                        {(game.minAge || game.playTimeMin || game.minPlayers) && (
                            <div className="flex items-center gap-3 mt-1">
                                {game.minAge && (
                                    <span className="flex items-center gap-1">
                                        <User className="w-3 h-3 flex-shrink-0" />
                                        <span>Wiek: {game.minAge}{game.maxAge ? `-${game.maxAge}` : '+'}</span>
                                    </span>
                                )}
                                {game.playTimeMin && (
                                    <span className="flex items-center gap-1">
                                        <Clock className="w-3 h-3 flex-shrink-0" />
                                        <span>
                                            {game.playTimeMax && game.playTimeMin !== game.playTimeMax
                                                ? `${game.playTimeMin}-${game.playTimeMax} min`
                                                : `${game.playTimeMin} min`}
                                        </span>
                                    </span>
                                )}
                                {game.minPlayers && (
                                    <span className="flex items-center gap-1">
                                        <Users className="w-3 h-3 flex-shrink-0" />
                                        <span>Gracze: {game.maxPlayers && game.minPlayers !== game.maxPlayers
                                            ? `${game.minPlayers}-${game.maxPlayers}`
                                            : `${game.minPlayers}`}
                                        </span>
                                    </span>
                                )}
                            </div>
                        )}

                        {/* Game Components - show only if any exist */}
                        {hasComponentCounts && (
                            <div className="mt-2 flex flex-wrap gap-2 text-xs">
                                {game.boardCount && game.boardCount > 0 && (
                                    <span className="flex items-center gap-1" title={`${game.boardCount} ${game.boardCount === 1 ? 'plansza' : game.boardCount < 5 ? 'plansze' : 'plansz'}`}>
                                        <Gamepad2 className="w-3 h-3 flex-shrink-0" />
                                        <span className="text-xs">{game.boardCount}</span>
                                    </span>
                                )}
                                {game.pieceCount && game.pieceCount > 0 && (
                                    <span className="flex items-center gap-1" title={`${game.pieceCount} ${game.pieceCount === 1 ? 'element' : game.pieceCount < 5 ? 'elementy' : 'elementw'}`}>
                                        <Puzzle className="w-3 h-3 flex-shrink-0" />
                                        <span className="text-xs">{game.pieceCount}</span>
                                    </span>
                                )}
                                {game.diceCount && game.diceCount > 0 && (
                                    <span className="flex items-center gap-1" title={`${game.diceCount} ${game.diceCount === 1 ? 'ko' : game.diceCount < 5 ? 'koci' : 'koci'}`}>
                                        <Dice5 className="w-3 h-3 flex-shrink-0" />
                                        <span className="text-xs">{game.diceCount}</span>
                                    </span>
                                )}
                                {game.cardCount && game.cardCount > 0 && (
                                    <span className="flex items-center gap-1" title={`${game.cardCount} ${game.cardCount === 1 ? 'karta' : game.cardCount < 5 ? 'karty' : 'kart'}`}>
                                        <ImageIcon className="w-3 h-3 flex-shrink-0" />
                                        <span className="text-xs">{game.cardCount}</span>
                                    </span>
                                )}
                                {game.figurineCount && game.figurineCount > 0 && (
                                    <span className="flex items-center gap-1" title={`${game.figurineCount} ${game.figurineCount === 1 ? 'figurka' : game.figurineCount < 5 ? 'figurki' : 'figurek'}`}>
                                        <User className="w-3 h-3 flex-shrink-0" />
                                        <span className="text-xs">{game.figurineCount}</span>
                                    </span>
                                )}
                            </div>
                        )}
                    </div>

                    <div className="flex flex-col">
                        <div className="flex flex-wrap gap-1">
                            {filteredTags
                                .slice(0, showAllTags ? filteredTags.length : 2)
                                .map(tag => (
                                <span key={tag} className="px-2 py-1 bg-blue-100 text-blue-800 text-xs rounded-full">
                                    {tag}
                                </span>
                            ))}
                        </div>

                        {filteredTags.length > 2 && (
                            <button
                                onClick={() => setShowAllTags(!showAllTags)}
                                className="text-xs text-blue-600 hover:text-blue-800 mt-1 flex items-center self-start"
                            >
                                {showAllTags ? (
                                    <>Poka mniej <X className="w-3 h-3 ml-1" /></>
                                ) : (
                                    <>Poka wszystkie {filteredTags.length} tagi</>
                                )}
                            </button>
                        )}
                    </div>

                    <div className="flex items-center justify-end gap-3">
                        <a
                            href={`http://46.248.162.95/opac/detail.php?PozycjaOpisy=${game.detailId}`}
                            target="_blank"
                            rel="noopener noreferrer"
                            className="bg-blue-600 hover:bg-blue-700 text-white px-3 py-1 rounded-md text-sm font-medium transition-colors duration-200 flex items-center gap-1"
                        >
                            <ExternalLink className="w-4 h-4" />
                            Szczegy
                        </a>
                    </div>
                </div>
            </div>
        </div>
    );
    };

    return (
        <div className="min-h-screen bg-gray-50">
            {/* Header */}
            <div className="bg-white shadow-sm border-b">
                <div className="max-w-7xl mx-auto px-4 py-6">
                    <div className="flex items-center justify-between mb-6">
                        <h1 className="text-3xl font-bold text-gray-900">Katalog Gier Planszowych</h1>
                        <div className="flex items-center gap-2">
                            <button
                                onClick={() => setViewMode('grid')}
                                className={`p-2 rounded-lg ${viewMode === 'grid' ? 'bg-blue-100 text-blue-600' : 'text-gray-600 hover:bg-gray-100'}`}
                            >
                                <Grid className="w-5 h-5" />
                            </button>
                            <button
                                onClick={() => setViewMode('list')}
                                className={`p-2 rounded-lg ${viewMode === 'list' ? 'bg-blue-100 text-blue-600' : 'text-gray-600 hover:bg-gray-100'}`}
                            >
                                <List className="w-5 h-5" />
                            </button>
                        </div>
                    </div>

                    {/* Search Bar and Component Filters */}
                    <div className="mb-4 grid grid-cols-1 lg:grid-cols-[1fr_auto] gap-4 items-center">
                        <div className="relative">
                            <Search className="absolute left-3 top-3 w-5 h-5 text-gray-400" />
                            <input
                                type="text"
                                placeholder="Szukaj gier po tytule..."
                                value={searchTerm}
                                onChange={(e) => setSearchTerm(e.target.value)}
                                className="w-full pl-10 pr-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none text-sm"
                            />
                        </div>

                        <div className="flex flex-wrap gap-2">
                            <button
                                onClick={() => setFilterBoardGames(!filterBoardGames)}
                                className={`px-3 py-2 text-sm rounded-lg border transition-colors duration-200 ${
                                    filterBoardGames
                                        ? 'bg-blue-600 text-white border-blue-600'
                                        : 'bg-white text-gray-700 border-gray-300 hover:border-blue-300 hover:bg-blue-50'
                                }`}
                                title="Gry z planszami"
                            >
                                <Gamepad2 className="w-3 h-3 inline mr-1" />
                                Plansze
                            </button>

                            <button
                                onClick={() => setFilterCardGames(!filterCardGames)}
                                className={`px-3 py-2 text-sm rounded-lg border transition-colors duration-200 ${
                                    filterCardGames
                                        ? 'bg-blue-600 text-white border-blue-600'
                                        : 'bg-white text-gray-700 border-gray-300 hover:border-blue-300 hover:bg-blue-50'
                                }`}
                                title="Gry z kartami"
                            >
                                <ImageIcon className="w-3 h-3 inline mr-1" />
                                Karty
                            </button>

                            <button
                                onClick={() => setFilterDiceGames(!filterDiceGames)}
                                className={`px-3 py-2 text-sm rounded-lg border transition-colors duration-200 ${
                                    filterDiceGames
                                        ? 'bg-blue-600 text-white border-blue-600'
                                        : 'bg-white text-gray-700 border-gray-300 hover:border-blue-300 hover:bg-blue-50'
                                }`}
                                title="Gry z komi"
                            >
                                <Dice5 className="w-3 h-3 inline mr-1" />
                                Koci
                            </button>

                            <button
                                onClick={() => setFilterFigurineGames(!filterFigurineGames)}
                                className={`px-3 py-2 text-sm rounded-lg border transition-colors duration-200 ${
                                    filterFigurineGames
                                        ? 'bg-blue-600 text-white border-blue-600'
                                        : 'bg-white text-gray-700 border-gray-300 hover:border-blue-300 hover:bg-blue-50'
                                }`}
                                title="Gry z figurkami"
                            >
                                <User className="w-3 h-3 inline mr-1" />
                                Figurki
                            </button>

                            <button
                                onClick={() => setFilterPieceGames(!filterPieceGames)}
                                className={`px-3 py-2 text-sm rounded-lg border transition-colors duration-200 ${
                                    filterPieceGames
                                        ? 'bg-blue-600 text-white border-blue-600'
                                        : 'bg-white text-gray-700 border-gray-300 hover:border-blue-300 hover:bg-blue-50'
                                }`}
                                title="Gry z elementami"
                            >
                                <Puzzle className="w-3 h-3 inline mr-1" />
                                Elementy
                            </button>
                        </div>
                    </div>

                    {/* Wybierz tagi - Collapsible */}
                    <div className="mb-4">
                        <button
                            onClick={() => setShowFilters(!showFilters)}
                            className="w-full flex items-center justify-between px-4 py-3 bg-gray-100 hover:bg-gray-200 rounded-lg transition-colors duration-200"
                        >
                            <div className="flex items-center gap-2">
                                <Tag className="w-4 h-4" />
                                <span className="text-sm font-medium">Wybierz tagi</span>
                            </div>
                            <div className="flex items-center gap-2">
                                {selectedTags.length > 0 && (
                                    <span className="bg-purple-600 text-white text-xs rounded-full px-2 py-1">
                                        {selectedTags.length}
                                    </span>
                                )}
                                {showFilters ? <ChevronUp className="w-4 h-4" /> : <ChevronDown className="w-4 h-4" />}
                            </div>
                        </button>

                        {showFilters && (
                            <div className="bg-gray-50 rounded-lg p-4 mt-2">
                                {/* Age category tags - always shown in first row */}
                                <div className="flex flex-wrap gap-2 mb-3">
                                    {['Doroli', 'Dzieci', 'Modzie'].map(tag => (
                                        allTags.includes(tag) && (
                                            <button
                                                key={tag}
                                                onClick={() => toggleTag(tag)}
                                                disabled={excludedTags.includes(tag)}
                                                className={`px-3 py-2 text-sm rounded-lg border transition-colors duration-200 ${
                                                    excludedTags.includes(tag)
                                                        ? 'bg-gray-100 text-gray-400 border-gray-200 cursor-not-allowed'
                                                        : selectedTags.includes(tag)
                                                            ? 'bg-purple-600 text-white border-purple-600'
                                                            : 'bg-white text-gray-700 border-gray-300 hover:border-purple-300 hover:bg-purple-50'
                                                }`}
                                            >
                                                <Tag className="w-3 h-3 inline mr-1" />
                                                {tag}
                                                <span className={`ml-1 text-xs ${selectedTags.includes(tag) ? 'text-purple-100' : 'text-gray-500'}`}>
                                                    ({tagCounts[tag] || 0})
                                                </span>
                                            </button>
                                        )
                                    ))}
                                </div>

                                {/* Other tags */}
                                <div className="flex flex-wrap gap-2">
                                    {allTags
                                        .filter(tag => !excludedTags.includes(tag) && !['Doroli', 'Dzieci', 'Modzie'].includes(tag))
                                        .slice(0, 25)
                                        .map(tag => (
                                        <button
                                            key={tag}
                                            onClick={() => toggleTag(tag)}
                                            disabled={excludedTags.includes(tag)}
                                            className={`px-3 py-2 text-sm rounded-lg border transition-colors duration-200 ${
                                                excludedTags.includes(tag)
                                                    ? 'bg-gray-100 text-gray-400 border-gray-200 cursor-not-allowed'
                                                    : selectedTags.includes(tag)
                                                        ? 'bg-purple-600 text-white border-purple-600'
                                                        : 'bg-white text-gray-700 border-gray-300 hover:border-purple-300 hover:bg-purple-50'
                                            }`}
                                        >
                                            <Tag className="w-3 h-3 inline mr-1" />
                                            {tag}
                                            <span className={`ml-1 text-xs ${selectedTags.includes(tag) ? 'text-purple-100' : 'text-gray-500'}`}>
                                                ({tagCounts[tag] || 0})
                                            </span>
                                        </button>
                                    ))}
                                </div>
                                {selectedTags.length > 0 && (
                                    <p className="text-xs text-gray-600 mt-2">
                                        Uwzgldnij gry z: {selectedTags.join(', ')}
                                    </p>
                                )}
                            </div>
                        )}
                    </div>

                    {/* Pomi tagi - Collapsible */}
                    <div className="mb-4">
                        <button
                            onClick={() => setShowExcludeFilters(!showExcludeFilters)}
                            className="w-full flex items-center justify-between px-4 py-3 bg-gray-100 hover:bg-gray-200 rounded-lg transition-colors duration-200"
                        >
                            <div className="flex items-center gap-2">
                                <EyeOff className="w-4 h-4" />
                                <span className="text-sm font-medium">Pomi tagi</span>
                            </div>
                            <div className="flex items-center gap-2">
                                {excludedTags.length > 0 && (
                                    <span className="bg-red-600 text-white text-xs rounded-full px-2 py-1">
                                        {excludedTags.length}
                                    </span>
                                )}
                                {showExcludeFilters ? <ChevronUp className="w-4 h-4" /> : <ChevronDown className="w-4 h-4" />}
                            </div>
                        </button>

                        {showExcludeFilters && (
                            <div className="bg-gray-50 rounded-lg p-4 mt-2">
                                <div className="flex flex-wrap gap-2">
                                    {allTags
                                        .filter(tag => !selectedTags.includes(tag))
                                        .slice(0, 25)
                                        .map(tag => (
                                        <button
                                            key={tag}
                                            onClick={() => toggleExcludedTag(tag)}
                                            disabled={selectedTags.includes(tag)}
                                            className={`px-3 py-2 text-sm rounded-lg border transition-colors duration-200 ${
                                                selectedTags.includes(tag)
                                                    ? 'bg-gray-100 text-gray-400 border-gray-200 cursor-not-allowed'
                                                    : excludedTags.includes(tag)
                                                        ? 'bg-red-600 text-white border-red-600'
                                                        : 'bg-white text-gray-700 border-gray-300 hover:border-red-300 hover:bg-red-50'
                                            }`}
                                        >
                                            <X className="w-3 h-3 inline mr-1" />
                                            {tag}
                                            <span className={`ml-1 text-xs ${excludedTags.includes(tag) ? 'text-red-100' : 'text-gray-500'}`}>
                                                ({tagCounts[tag] || 0})
                                            </span>
                                        </button>
                                    ))}
                                </div>
                                {excludedTags.length > 0 && (
                                    <p className="text-xs text-red-600 mt-2">
                                        Ukrywanie gier z: {excludedTags.join(', ')}
                                    </p>
                                )}
                            </div>
                        )}
                    </div>

                    {/* Sort By */}
                    <div className="mb-6">
                        <label className="block text-sm font-medium text-gray-700 mb-2">Sortuj wedug</label>
                        <select
                            value={sortBy}
                            onChange={(e) => setSortBy(e.target.value)}
                            className="w-full border border-gray-300 rounded-lg px-3 py-2 text-sm focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none"
                        >
                            <option value="title">Tytu</option>
                            <option value="author">Autor</option>
                            <option value="year">Rok (Najnowsze)</option>
                            <option value="publisher">Wydawca</option>
                        </select>
                    </div>

                    {/* Results Count */}
                    <div className="flex items-center justify-between mb-4">
                        <p className="text-sm text-gray-600">
                            Pokazywanie {filteredGames.length} z {games.length} gier
                        </p>
                    </div>
                </div>
            </div>

            {/* Game Grid/List */}
            <div className="max-w-7xl mx-auto px-4 py-6">
                {filteredGames.length === 0 ? (
                    <div className="text-center py-12">
                        <div className="text-gray-400 mb-4">
                            <Search className="w-16 h-16 mx-auto" />
                        </div>
                        <h3 className="text-lg font-medium text-gray-900 mb-2">Nie znaleziono gier</h3>
                        <p className="text-gray-600 mb-4">
                            Sprbuj dostosowa wyszukiwane hasa lub filtry
                        </p>
                        <button
                            onClick={clearFilters}
                            className="text-blue-600 hover:text-blue-800 font-medium"
                        >
                            Wyczy wszystkie filtry
                        </button>
                    </div>
                ) : (
                    <>
                        {viewMode === 'grid' ? (
                            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
                                {filteredGames.map(game => (
                                    <GameCard key={game.detailId} game={game} />
                                ))}
                            </div>
                        ) : (
                            <div className="space-y-4">
                                {filteredGames.map(game => (
                                    <GameRow key={game.detailId} game={game} />
                                ))}
                            </div>
                        )}
                    </>
                )}
            </div>
        </div>
    );
};

export default BoardGameCatalog;
