import { GameData } from '../types/GameData';
import mockGamesData from '../data/mockGames.json';

// Cast the imported JSON to the correct type
const mockGames: GameData[] = mockGamesData as GameData[];

/**
 * Game API service
 * This is an abstraction layer that will allow us to easily switch
 * from static JSON to REST API in the future
 */
export class GameApiService {
    /**
     * Get all games from the catalog
     * Currently using mock data from JSON file, will be replaced with API call later
     */
    async getAllGames(): Promise<GameData[]> {
        // In the future, this will be:
        // return fetch('/api/games').then(res => res.json());
        return Promise.resolve(mockGames);
    }

    /**
     * Get a specific game by ID
     * @param id The game ID
     */
    async getGameById(id: string): Promise<GameData | undefined> {
        // In the future, this will be:
        // return fetch(`/api/games/${id}`).then(res => res.json());
        return Promise.resolve(mockGames.find(game => game.detailId === id));
    }

    /**
     * Get all unique tags from the catalog
     */
    async getAllTags(): Promise<string[]> {
        // In the future, this will be:
        // return fetch('/api/tags').then(res => res.json());
        const tags = new Set<string>();
        mockGames.forEach(game => {
            if (game.tags) {
                game.tags.forEach(tag => tags.add(tag));
            }
        });
        return Promise.resolve(Array.from(tags).sort());
    }

    /**
     * Get all unique authors from the catalog
     */
    async getAllAuthors(): Promise<string[]> {
        // In the future, this will be:
        // return fetch('/api/authors').then(res => res.json());
        const authors = new Set(mockGames.map(game => game.author).filter(author => author.trim() !== ''));
        return Promise.resolve(Array.from(authors).sort());
    }

    /**
     * Get all unique publishers from the catalog
     */
    async getAllPublishers(): Promise<string[]> {
        // In the future, this will be:
        // return fetch('/api/publishers').then(res => res.json());
        const publishers = new Set(mockGames.map(game => game.publisherName));
        return Promise.resolve(Array.from(publishers).sort());
    }
}

// Create a singleton instance to be used throughout the app
export const gameApiService = new GameApiService();
